    line-height: 18px;\n    color: #7b7b7b;\n}\n.venue-list-all {\n    display: grid !important; \n    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr)); \n    grid-template-rows: max-content; \n    grid-column-gap: 10px; \n    grid-row-gap: 20px;\n}\n.venue-list-single {\n    width: 80%;\n    display: flex;\n    padding: 10px;\n    background: #ffffff;\n    border: 0.5px solid #d1d1d1;\n    box-shadow: 0px 0px 0.5px rgba(0, 0, 0, 0.25);\n    margin-bottom: 13px;\n}\n.venue-list-single-descp {\n    width: 60%;\n    padding: 18px;\n    padding-top: 30px;\n    text-decoration: none;\n}\n.venue-list-single-descp > h1 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 19px;\n    line-height: 28px;\n    color: #000000;\n}\n.venue-list-single-descp > h2 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 300;\n    font-size: 13px;\n    line-height: 20px;\n    color: #828282;\n    margin-bottom: 10px;\n}\n.venue-feature-list {\n    display: flex;\n}\n.venue-feature-list > span:nth-child(1) {\n    width: 130px;\n}\n.venue-feature-list > span:nth-child(1) > img {\n    margin-right: 10px;\n    width: 11px;\n    height: 9px;\n}\n.venue-feature-list > span {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 300;\n    font-size: 10.439px;\n    line-height: 16px;\n    color: #636363;\n}\n.venue-feature-list > span:last-child {\n    color: #000000;\n}\n.venue-list-single-descp > p {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 300;\n    font-size: 9.77876px;\n    line-height: 15px;\n    color: #000000;\n}\n.venue-tags-cont {\n    display: flex;\n    flex-wrap: wrap;\n}\n.venue-tags-cont > span {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 9.77876px;\n    line-height: 15px;\n    color: #353232;\n    padding: 4px 6px;\n    background: #f1f5fa;\n    border-radius: 5px;\n    margin-right: 7px;\n    margin-bottom: 7px;\n}\n.venue-price-food {\n    display: flex;\n    justify-content: space-between;\n}\n.venue-food {\n    display: flex;\n}\n.venue-food > div:nth-child(1) {\n    margin-right: 30px;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n}\n.venue-food > div:nth-child(2) {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n}\n.venue-food > div:nth-child(1) > div > img {\n    margin-right: 7px;\n}\n.venue-food > div:nth-child(1) > div {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 9.90941px;\n    line-height: 15px;\n    color: #898484;\n}\n.venue-food > div:nth-child(2) > div {\n    color: #898484;\n}\n.venue-food > div:nth-child(2) > div > span {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12.8455px;\n    line-height: 19px;\n    color: #000000;\n}\n.venue-price-food > button {\n    border: none;\n    outline: none;\n    background: #461952;\n    border-radius: 4px;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 9.36504px;\n    line-height: 14px;\n    padding: 10px;\n    color: #ffffff;\n}\n@media only screen and (max-width: 992px) {\n    .venue-filter-cont > div {\n        overflow-x: auto;\n    }\n    .venue-filter-single {\n        min-width: 137px;\n        margin-bottom: 10px;\n        display: none;\n    }\n}\n@media only screen and (max-width: 768px) {\n    .venue-list-single {\n        width: 100%;\n    }\n    .venue-list-head {\n        flex-wrap: wrap;\n        justify-content: center;\n    }\n    .venue-list-head > div {\n        width: 100%;\n    }\n}\n@media only screen and (max-width: 655px) {\n    .venue-list-single {\n        width: 100%;\n    }\n    .venue-list-single {\n        flex-wrap: wrap;\n    }\n    .venue-list-single > a {\n        width: 100% !important;\n    }\n    .venue-list-single > div {\n        width: 60% !important;\n        margin: auto;\n    }\n}\n@media only screen and (max-width: 576px) {\n    /* .venue-search-cont {\n        min-height: 300px;\n    } */\n    .venue-list-single > div {\n        width: 90% !important;\n        margin: auto;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
